import { Component } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { CrudService } from 'src/app/services/crud.service';

@Component({
  selector: 'app-knowledge-base',
  templateUrl: './knowledge-base.component.html',
  styleUrls: ['./knowledge-base.component.scss']
})
export class KnowledgeBaseComponent {
  articleAnswerView: boolean = true;
  answerDetail: any;
  Ques: any = this.ar.queryParams;

  constructor(private crud: CrudService, private sanitizer: DomSanitizer, private ar: ActivatedRoute,
    ) { }

  ngOnInit() {
    if(this.Ques['_value'].ques){
      this.getArticleDetail(this.Ques['_value'].ques);
    }
  }
  getArticleDetail(id: string, type?: string) {
    this.crud.getRequest(`${requestUrl.getArticleDetail}${id}`).then((res: any) => {
      this.answerDetail = res;
      this.answerDetail.answer = this.sanitizer.bypassSecurityTrustHtml(this.answerDetail?.answer);
      this.articleAnswerView = true;
    })
  }
}
